const inputValidation = inputUser => {
  let errors = {};

  //checking if input field is empty
  if (!inputUser.name) {
    errors.name = "Navn er pkrevd";
  }
  if (!inputUser.email) {
    errors.email = "Epost er pkrevd";
  } else if (!/\S+@\S+\.\S+/.test(inputUser.email)) {
    errors.email = "Epost er ugyldig";
  }
  if (!inputUser.number) {
    errors.number = "Nummer er pkrevd";
  }
  if (!inputUser.message) {
    errors.message = "Melding hadde vrt fint:)";
  }
  return errors;
};

export default inputValidation;
